import React from "react";
import { useSpring, animated } from "react-spring";

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2];
const background = (x, y) => `translate3d(${x / 155}px,${y / 155}px,0)`;
const heading = (x, y) => `translate3d(${x / 18}px,${y / 18 + 20}px,0)`;
const subheading = (x, y) => `translate3d(${x / 25}px,${y / 25 + 30}px,0)`;

function Header() {
    const [props, set] = useSpring(() => ({
        xy: [0, 0],
        config: { mass: 10, tension: 550, friction: 140 },
    }));
    return (
        <div
            onMouseMove={({ clientX: x, clientY: y }) =>
                set({ xy: calc(x, y) })
            }
        >
            <animated.div
                className="header"
                style={{ transform: props.xy.interpolate(background) }}
            >
                <animated.h1
                    className="heading"
                    style={{ transform: props.xy.interpolate(heading) }}
                >
                    Hi, I'm Baber Parweez
                </animated.h1>

                <animated.h3
                    className="subheading"
                    style={{ transform: props.xy.interpolate(subheading) }}
                >
                    Web Developer <span>|</span> Coder <span>|</span> Music
                    Producer
                </animated.h3>
            </animated.div>
        </div>
    );
}

export default Header;
